:root {
  --accent-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.header_link {
  width: 40px;
  height: 40px;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav_item:last-child .nav_link {
  margin-right: 0;
}

.nav_link:hover {
  color: #00acdc;
}

.is-active {
  color: var(--accent-color);
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
